import axios from "axios"
import qs from "qs"

var baseUrl="/api"

//
axios.interceptors.request.use(config=>{
    var user=JSON.parse(sessionStorage.getItem("user") || '{}') 
    if(user.token){
        config.headers.authorization=user.token  
    }
    return config;
   
})

//
axios.interceptors.response.use(res=>{
    console.group("",res.config.url);
    console.log(res.data);
    return res.data;
})



//
export const bannerList=()=>{
    return axios({
        method:"get",
        url:baseUrl+"/api/getbanner"
    })
}

//
export const indexGoods=()=>{
    return axios({
        method:"get",
        url:baseUrl+"/api/getindexgoods"
    })
}

//
export const cart=()=>{
    return axios({
        method:"get",
        url:baseUrl+"/api/getcatetree"
    })
}

//
export const goods=(params)=>{
    return axios({
        method:"get",
        url:baseUrl+"/api/getgoods",
        params
    })
}

// 
export const register=(data)=>{
    return axios({
        method:"post",
        url:baseUrl+"/api/register",
        data:qs.stringify(data)
    })
}
//
export const login=(data)=>{
    return axios({
        method:"post",
        url:baseUrl+"/api/login",
        data:qs.stringify(data)
    })
}
//
export const shoppList=(params)=>{
    return axios({
        method:"get",
        url:baseUrl+"/api/cartlist",
        params
    })
}

//
export const listInfo=(params)=>{
    return axios({
        method:"get",
        url:baseUrl+"/api/getgoodsinfo",
        params
    })
}
//
export const shoppAdd=(data)=>{
    console.log(data);
    return axios({
        method:"post",
        url:baseUrl+"/api/cartadd",
        data:qs.stringify(data)
    })
}
//
export const shoppDel=(data)=>{
  
    return axios({
        method:"post",
        url:baseUrl+"/api/cartdelete",
        data

    })
}


